import _ from 'lodash'

import Paginate from '../../mixins/paginate'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Paginate
  ],

  data() {
    return {
      action: null,
      list: {
        data: [],
        recordsFiltered: 0
      },
      subAgent: {
        id: null,
        account_no: null,
        username: '',
        name: '',
        lname: '',
        commission: 0,
        status: 1,
        password: ''
      },
      rules: {
        username: [
          {
            required: true,
            message: 'Please input username',
            trigger: 'blur'
          }
        ],
        contact_no: [
          {
            required: true,
            message: 'Please input contact #',
            trigger: 'blur'
          }
        ],
        // name: [
        //   {
        //     required: true,
        //     message: 'Please input first name',
        //     trigger: 'blur'
        //   }
        // ],
        // lname: [
        //   {
        //     required: true,
        //     message: 'Please input last name',
        //     trigger: 'blur'
        //   }
        // ],
        password: [
          {
            required: true,
            message: 'Please input password',
            trigger: 'blur'
          }
        ]
      },
      showDialog: false,
      isLoading: true,
      isFetching: false,
      isSaving: false,
      route,
    }
  },

  computed: {
    dialogTitle() {
      return this.action == 'add' ?
        'Add new sub-agent' : 'Edit sub-agent'
    },

    tableHeight() {
      return window.innerHeight - 250
    },
  },

  mounted() {
    this.fetch()
  },

  methods: {
    handleClose(done) {
      this.action = null
      this.subAgent = {
        id: null,
        account_no: null,
        username: '',
        name: '',
        lname: '',
        commission: 0,
        status: 1,
        password: ''
      }

      if (typeof done == 'function') {
        done()
      }
    },

    isNumber(evt) {
      evt = (evt) ? evt : window.event
      let charCode = (evt.which) ? evt.which : evt.keyCode

      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },

    openDialog(action) {
      this.action = action
      this.showDialog = true
    },

    closeDialog() {
      this.handleClose()
      this.showDialog = false
    },

    async fetch() {
      this.isLoading = true

      try {
        let params = {
          start: (this.currPage - 1) * this.currPageSize,
          length: this.currPageSize,

          // Global search
          'search[value]': this.search,

          // Account #
          'columns[0][data]': 'account_no',
          'columns[0][name]': 'account_no',
          'columns[0][searchable]': true,
          'columns[0][orderable]': true,
          'columns[0][search][value]': '',
          'columns[0][search][regex]': true,

          // Username #
          'columns[1][data]': 'username',
          'columns[1][name]': 'username',
          'columns[1][searchable]': true,
          'columns[1][orderable]': true,
          'columns[1][search][value]': '',
          'columns[1][search][regex]': true,

          // Commission
          'columns[2][data]': 'commission',
          'columns[2][name]': 'commission',
          'columns[2][searchable]': true,
          'columns[2][orderable]': true,
          'columns[2][search][value]': '',
          'columns[2][search][regex]': true,

          // Credit balance
          'columns[3][data]': 'credit',
          'columns[3][name]': 'credit',
          'columns[3][searchable]': true,
          'columns[3][orderable]': true,
          'columns[3][search][value]': '',
          'columns[3][search][regex]': true,
        }

        this.list = await this.$store.dispatch('subagent/list', params)
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async remove(id) {
      try {
        await this.$store.dispatch('subagent/remove', id)

        this.fetch()
        this.$message({
          type: 'success',
          message: 'Delete Successful'
        })
      } catch(e) {
        this.$message({
          type: 'error',
          message: 'Unable to remove.'
        })
      }
    },

    async edit(id) {
      this.isLoading = true

      try {
        this.subAgent = await this.$store.dispatch('subagent/get', id)
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
      this.openDialog('edit')
    },

    async save() {
      if (this.isSaving) return

      this.$refs.form.validate(async valid => {
        if (!valid || this.isSaving) {
          return
        }

        this.isSaving = true

        try {
          delete this.subAgent.account_no
          delete this.subAgent.pa_id

          await this.$store.dispatch('subagent/save', this.subAgent)
          let message = this.action == 'add' ?
            'Add Successful' : 'Update Successful'

          this.fetch()
          this.closeDialog()
          this.$message({
            type: 'success',
            message
          })
        } catch(e) {
          this.$message({
            type: 'error',
            message: e.message
          })
        }

        this.isSaving = false
      })
    }
  }
}



